.about {
    position: relative;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(50% , 1fr));
    /**grid-template-rows: minmax(100px, auto);**/
    margin: 40px;
    grid-auto-flow: dense;
    /**grid-gap: 20px;**/
}

.about .box {
    padding: 20px;
    display: grid;
    font-size: 20px;
    place-items: center;
    text-align: center;
    height: 80vh;
}

.about .box:nth-child(1) {
    background-color: #212121;
    border-radius: 15px;
}

.about .box:nth-child(1) * {
    color: #fff;
    margin-bottom: 20px;
}

.about .box p {
    padding: 10px 10%;
    text-align: justify;
}
.about .box img {
    position: relative;
    max-height: 30vh;
    margin-bottom: 10px;
    border-radius: 100%;
	-webkit-border-radius: 100%;
    -moz-border-radius: 100%;
}

@media all and (max-width: 900px){
    .about {
        margin-top: 10vh;
        grid-template-columns: repeat(auto-fill, minmax(100% , 1fr));
    }
}