.container {
    position: relative;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(4, minmax(22.5% , 1fr));
    padding: 0 5%;
    grid-gap: 2%;
    overflow: hidden;
    height: 90vh;
    /** border: 1px solid black;**/
}

.container .box {
    position: relative;
    display: grid;
    font-size: 20px;
    place-items: center;
    text-align: center;
    height: 40vh;
    transition: 0.5s;
    overflow: hidden;
}

.container .box:nth-child(1),  .container .box:nth-child(2){
    grid-column: span 2;
}

.container .box .imgBx {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: 0.5s;
    padding: 50px;
}

.container .box .imgBx img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border-radius: 15px;
}

.container .box .content {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
    padding: 20px;
    border-radius: 15px;
    opacity: 0;
    transition: 0.5s;
    /* 212121 */
    background-color: rgba(33, 33, 33, 0.9);
}

.container .box .content h2 {
    margin-top: 16vh;
    margin-bottom: 13px;
    color: #fff;
}

.container .box .content p {
    color: #fff;
    text-align: center;
}

.container .box:hover .content {
    opacity: 1;
}

.container .box .content button{
    margin-top: 10px;
    padding: 0.5rem;
    background-color: red;
    border: 2px solid red;
    border-radius: 5px;
    color: #fff;
    white-space: nowrap;
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: 0.5s;
}

.container .box .content button:hover {
    color: red;
    background-color: white;
    cursor: pointer;
}

@media all and (max-width: 900px){
    .container {
        margin-top: 10vh;
    }
}

@media all and (max-width: 1200px){
    .container {
        grid-template-columns: repeat(auto-fill, minmax(45% , 1fr));
        height: 130vh;
    }
    .container .box:nth-child(1),  .container .box:nth-child(2){
        grid-column: span 1;
    }
}

@media all and (max-width: 700px){
    .container .box:nth-child(1),  .container .box:nth-child(2){
        grid-column: span 1;
    }
    .container {
        grid-template: repeat(6, 15%) / 100%;
        height: 270vh;
    }
}