* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  color: #212121;
  text-decoration: none;
}

body {
  height: 100vh;
  background-color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
}

nav {
  height: 10vh;
  width: 100%;
}

label.brand {
  font-size: 2em;
  font-weight: bold;
  line-height: 10vh;
  margin-left: 5%;
}

nav ul {
  float: right;
  margin-right: 5%;
}

nav ul li {
  display: inline-block;
  line-height: 10vh;
  padding:  0 2rem;
}

.checkbtn {
  font-size: 30px;
  float: right;
  line-height: 10vh;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}

#check {
  display: none;
}

@media all and (max-width: 900px){
  nav {
      top: 0;
      position: fixed;
      z-index: 2;
      background-color: #fff;
  }
  .checkbtn {
      display: block;
  }
  ul {
      position: fixed;
      width: 150px;
      height: 100vh;
      top: 10vh;
      left: -100%;
      text-align: center;
      transition: 0.5s;
      background-color: #ffffff;
      box-shadow: 1px 11px 10px #333333;
  }
  nav ul li {
      display: block;
  }
  #check:checked ~ ul {
      left: 0;
      z-index: 1;
  }
}